export default {
	data() {
		return {
			phone: "",
			captcha: "",
			rules: { // 正则验证
				phone: [{
					rule: /^[1][3-9]\d{9}$/,
					msg: "请输入正确的11位手机号"
				}],
				captcha: [{
					rule: /^\d{4}$/,
					msg: "请输入您的验证码"
				}]
			},
			safety: {
				time: 10,
				state: false,
				interval: ''
			},
		}
	},
	
	methods: {
		// 点击发送验证码
		getCode() {
			if ((this.rules.phone[0].rule).test(this.phone)) {
				uni.showToast({
					title: "正在发送验证码",
					icon: "loading",
					success: () => {
						// 成功后显示倒计时60s后可在点击
						this.safety.state = true;
						// 倒计时
						this.safety.interval = setInterval(() => {
							if (this.safety.time-- <= 0) {
								this.safety.time = 10;
								this.safety.state = false;
								clearInterval(this.safety.interval);
							}
						}, 1000);
						uni.showToast({
							title: "发送成功",
							icon: "success"
						})
						this.$H.post("/agent/", {
							mobile: this.phone,
							opt: verify_code,
						}).then((res) => {
							
						}).catch((e) => {
							console.log("catch error!!", e);
						})
					}
				})
			} else {
				uni.showToast({
					title: "手机号不正确",
					icon: "none"
				})
			}
		},
		// 登录
		login() {
			this.$H.post("/agent/", {
				mobile: this.phone,
				opt: verify_login,
				verify_code: this.captcha
			}).then((res) => {
				if(res) {
					try {
					    uni.setStorageSync('user_id', res.user_id);
						uni.setStorageSync('token', res.token);
						uni.navigateTo({
							url: "/pages/index/index"
						})
					} catch (e) {
					    console.log('error catch:', e);
					}
				}
				console.log('res:', res);
			}).catch((e) => {
				console.log("catch error!!", e);
			})
			this.__init()
		},
		
	}
}
